import React, { Component } from "react"
// import PropTypes from 'prop-types'

import {
  // Card,
  Col,
  // Container,
  Modal,
  Row,
  ModalBody,
  // CardBody,
  // CardHeader,
  // CardTitle,
  // FormGroup,
  // Label,
  // Button,
  // Form,
  Input,
 
}  from "reactstrap"
// import ReactSelect from 'react-select'
// import makeAnimated from 'react-select/animated';
// import { format } from 'date-fns';
import $ from 'jquery';
import conf from '../conf.js';
// import  { Redirect } from 'react-router-dom'
// import {postQueries} from '../queries/postQueries'
import ModalMessage from "../views/pages/messages/ModalMessage"

  class ModalFormRemboursement extends Component {
    constructor(props) {
      super(props)
    
      this.state = {
        ok:"",
        prix:{},
        type:"",
        montant:0,
        show:false,
        show_form:this.props.show,
        success:true,
        boutton:"OK",
        message:"Demande envoy avec succs !!",
        titre:"Message demande de fond",

      }
      this.handleChangeMontant.bind(this)
    }

    componentDidMount() {
      let  user=  JSON.parse(localStorage.getItem('token_auth')).user
      $("#reference").val(user.compteBancaire)
      // var id=localStorage.getItem("idTerrain")
      const requestOptions = {
        method: 'GET',
        headers: { 
          'Authorization':'Bearer '+JSON.parse(localStorage.getItem('token_auth')).token,
    
        },
       
      };
  
      fetch(conf.url+"reservation/solde/"+user.id,requestOptions)
      .then((response) => response.json())
      .then((data) => {
       
        if(data.idProprietaire){
          var montant =parseInt(data.montantTotalReservations)-parseInt(data.montantTotalDemandeVersement)-parseInt(data.montantTotalReservations)*1.5/100
          this.setState({montant:montant})
        }
        
        })
    }

    handleChangeMontant = (e) => {
      
      if(parseInt(e.target.value)>this.state.montant){
        $("#montant").val(this.state.montant)
      }
    
    }

    handleSave=()=>{
      let  token=  JSON.parse(localStorage.getItem('token_auth'))
     
   
  
      var data={
        "date":new Date(),
        "montant": $("#montant").val(),
        "status": "EN ATTENTE",
        "typeVersement": "",
        "reference": $("#reference").val()
     }
    

     const requestOptions = {
       method: 'POST',
       headers: { 
         'Content-Type': 'application/json','Accept': 'application/json' ,
         'Authorization':'Bearer '+JSON.parse(localStorage.getItem('token_auth')).token,
   
       },
       body: JSON.stringify(data)
     };
 
     fetch(conf.url+"user/add/versement/user/"+token.user.id,requestOptions)
     .then((response) => response.json())
     .then((data) => {
   
       if(data.id){
         this.setState({show:true,success:true,show_form:false})
         window.location.reload(false);
       }else{
         this.setState({message:"une erreur est survenue!!",titre:"erreur"})
         this.setState({show:true,success:false})
       }
     })

    }
    handleValidate=()=>{
     
        this.setState({show:false})
      
      
    }
  render (){
  const show =this.props.show
  const onCloseClick=this.props.onCloseClick
  // const onValidate=this.props.onValidate
  // const animatedComponents = makeAnimated();

  // const type = [
  //   {
      
  //     options: [
  //       { label: "MOBILE MONEY", value: "MOBILE MONEY" },
  //       { label: "VIREMENT BANCAIRE", value: "VIREMENT BANCAIRE" },
  //       { label: "ESPECE", value: "ESPECE" },
  //       { label: "AUTRE", value: "AUTRE" },
  //     ]
  //   },
    
  // ]

  return (
    <Modal isOpen={show} toggle={onCloseClick} centered={true}>
         <ModalMessage
              show={this.state.show}
              success={this.state.success}
              boutton={this.state.boutton}
              handleValidate={this.handleValidate}
              message={this.state.message}
              titre={this.state.titre}
              onCloseClick={() => this.setState({ show: false })}
              />


      <ModalBody className="py-3 px-5">     {this.state.ok}
        <Row className="mb-3">
          <Col lg={12}>
            <div >
              <i className="bx bx-football text-success display-5" style={{ lineHeight : '1'}}/>
          
            </div>
          </Col>
        </Row>
        <Row className="mb-3">

        <Col md="12" sm="12">
            <label>Montant</label>
            <Input
                  className="form-control"
                  type="number"
                  id="montant"
                  placeholder="montant demand"
                  onChange={this.handleChangeMontant}
              />
          </Col>
      
          
        </Row>
        {/* <Row className="mb-3">

          <Col md="12" sm="12">
            <label>Type de paiement</label>
            <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeType}
                          options={type}
                          placeholder="Type de paiement"
                        />
          </Col>
  
        </Row> */}

        <Row className="mb-3">
       
         <Col md="12" sm="12">
           <label>Numro du compte</label> 
            <Input
                  className="form-control"
                  type="text"
                  id="reference"
                  
              />
          </Col>
          
     
        </Row>
        <Row>
        <Col>
            <div className="mt-3" style={{float:"left"}}>
              
              <button
                type="button"
                className="btn btn-danger btn-sm me-2"
                onClick={onCloseClick}
              >
                Annuler
              </button>
            </div>
          </Col>
          <Col>
            <div className="mt-3" style={{float:"right"}}>
              
              <button
                type="button"
                className="btn btn-success btn-sm me-2"
                onClick={this.handleSave}
              >
                Envoyer
              </button>
            </div>
          </Col>
        </Row>
        
      </ModalBody>
    </Modal>
  )
}


  }
export default ModalFormRemboursement
