/* eslint-disable no-unused-vars */
import React, { Component } from "react"
// import MetaTags from 'react-meta-tags';
//import { format } from 'date-fns';
import { MDBDataTable } from "mdbreact"


import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  // CardHeader,
  // CardTitle,
  // FormGroup,
  // Label,
  Button,
  // Badge,
  // Form,
  // Input,
  // InputGroupAddon,
  // InputGroupText,
  InputGroup,
  // Breadcrumb,
  // BreadcrumbItem,
} from "reactstrap"
import Flatpickr from "react-flatpickr"
import { French } from "flatpickr/dist/l10n/fr.js";
// import Select from "react-select"
// import axios from 'axios'
// import  { Redirect } from 'react-router-dom'
//  import $ from "jquery";

import { format } from 'date-fns';
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
//import BootstrapSelect from "bootstrap-select"
import conf from '../../conf.js'
//import Confirmer from '../../../components/Confirmer.js' 
import ModalFormRemboursement from '../../components/ModalFormRemboursement.js'  
// import  { Redirect } from 'react-router-dom'

class Finance extends Component {


  constructor(props) {
    super(props)
    this.state = {
      users:[],
      demande:[],
      userData:[],
      terrain:[],
      reservation:[],
      versement:[],
      id:0,
      show:false,
      color_danger:"#e55353",
      color_success:"#194D33",
      color_warning: "#fd7e14",
      ok:"",
      solde:{nombre_reservation:0,total:0},
      solde_reservation_versement:{idProprietaire:0,montantTotalReservations:0,montantTotalDemandeVersement:0}
    }
    this.handleChangeTerrain.bind(this)
    this.handleDateInterval.bind(this)
    this.handleCalculTotal.bind(this)
    this.handleCalculPourcentage.bind(this)
  }
  componentDidMount() {

    let  user=  JSON.parse(localStorage.getItem('token_auth')).user
    var id=localStorage.getItem("idTerrain")
    const requestOptions = {
      method: 'GET',
      headers: { 
        'Authorization':'Bearer '+JSON.parse(localStorage.getItem('token_auth')).token,
  
      },
     
    };

    fetch(conf.url+"terrain/terrains",requestOptions)
    .then((response) => response.json())
    .then((data) => {
    
      var terrain=[]
      var nbr=0
      data.filter(el=>el.idUser==user.id).map((val,idx)=>{
        const reserv=val.reservations.filter((filtre)=>filtre.statePayement==="PAYE")
        nbr+=reserv.length
        this.setState({solde:{nombre_reservation:nbr,total:this.state.solde.total}})
        this.setState({terrain:[...this.state.terrain,{value:val.id,label:val.nom}]})
       })

      })

   


    fetch(conf.url+"reservation/solde/"+user.id,requestOptions)
    .then((response) => response.json())
    .then((data) => {
     
      if(data.idProprietaire){
        
        this.setState({solde_reservation_versement:data,solde:{nombre_reservation:this.state.solde.nombre_reservation,total:data.montantTotalReservations}})
      }
      
      })

      fetch(conf.url+"versement/versements/user/"+user.id,requestOptions)
      .then((response) => response.json())
      .then((data) => {
       console.log("reserv",data)
        if(data.length){
        
          data.map((val,idx)=>{
          var color=""
          if(val.status==="EN ATTENTE"){
            color=this.state.color_warning
          }
          if(val.status==="VALIDE"){
            color=this.state.color_success
          }

          if(val.status==="REJETE"){
            color=this.state.color_danger
          }
         
          var tab= {
            id:val.id,
            date: format(new Date(val.date),'dd/MM/yyyy'),
            prix: new Intl.NumberFormat().format(val.montant)+ " FCFA",
            reference: val.reference,
            typedepaiement: val.typeVersement,
            status:<span style={{backgroundColor:color,textAlign:'center',float:'center'}} class="badge text-center">EN ATTENTE</span>,
           
            //typesystemeirrigue: element.nom,
         
            // action:  <a> 
            //            <i style={{cursor:"pointer"}}  className="bx bx-pencil bx-sm text-primary mr-2"/> 
                      
            //            <i style={{cursor:"pointer"}}  className="bx bx-trash  bx-sm text-danger "/> 
            //          </a>,
            
          }
          this.setState({versement:[...this.state.versement,tab]})
        })
        }
        
      
        })


  };


  handleStart=()=>{
    this.setState({show:true})
  }

  handleChangeTerrain=(e)=>{
    this.setState({solde:{nombre_reservation:0,total:0}})
    this.handleCalculTotal(e.value,null,null)
     

  }
  handleDateInterval= (date)=> {
    
    if(date.length===2){
      
      this.setState({solde:{nombre_reservation:0,total:0}})
      this.handleCalculTotal(this.state.id,date[0],date[1])
    }
  
  
  }
  handleCalculPourcentage=(solde_initial,solde_versement)=>{
   return parseInt(solde_initial)-parseInt(solde_versement)-parseInt(solde_initial)*1.5/100
  }
  handleCalculTotal=(id,date1,date2)=>{
    let  user=  JSON.parse(localStorage.getItem('token_auth')).user
    const requestOptions = {
      method: 'GET',
     
    };
   
    this.setState({id:id})

    fetch(conf.url+"terrain/terrains/"+id,requestOptions)
    .then((response) => response.json())
    .then((data) => {
    
      var terrain=[]
      var solde=0
      const reserv1=data.reservations.filter((filtre)=>filtre.statePayement==="PAYE")
      
      if(!date1 && this.state.reservation.length===0){
        reserv1.map((element,idx)=>{
         solde+=parseInt(element.montantTotal)
         this.setState({solde:{nombre_reservation:parseInt(idx)+1,total:solde},reservation:reserv1})
         })
        }
        if(!date1 && this.state.reservation.length){
          this.state.reservation.map((element,idx)=>{
           solde+=parseInt(element.montantTotal)
           this.setState({solde:{nombre_reservation:parseInt(idx)+1,total:solde}})
           })
          }

      if(date1 && this.state.reservation.length){
        this.state.reservation.filter((filtre)=>(new Date(filtre.dateReservationTypeDate)>=date1) && (new Date(filtre.dateReservationTypeDate)<=date2)).map((element,idx)=>{
          solde+=parseInt(element.montantTotal)
          this.setState({solde:{nombre_reservation:parseInt(idx)+1,total:solde}})
          })
      }

      if(date1 && this.state.reservation.length===0){
        const reser2= data.reservations.filter((filtre)=>(filtre.statePayement==="PAYE") && (new Date(filtre.dateReservationTypeDate)>=date1) && (new Date(filtre.dateReservationTypeDate)<=date2))
       
        reser2.map((element,idx)=>{
          solde+=parseInt(element.montantTotal)
          this.setState({solde:{nombre_reservation:parseInt(idx)+1,total:solde},reservation:reser2})
          })
      }

       })

  }

  render() {
    const { pick } = this.state
    const animatedComponents = makeAnimated();
    const data = {
      columns: [
       
        {
          label: "Date",
          field: "date",
          sort: "asc",
          width: 100,
        },
        
        {
          label: "Montant",
          field: "prix",
          sort: "asc",
          width: 100,
        },
        
        {
          label: "Type de paiement",
          field: "typedepaiement",
          sort: "asc",
          width: 100,
        },
        {
          label: "rfrence",
          field: "reference",
          sort: "asc",
          width: 100,
        },
        {
          label: "Status Paiement",
          field: "status",
          sort: "asc",
          width: 100,
        }
      ],
      rows:this.state.versement
      
    }


    return (
      <React.Fragment>
        <div className="page-content"> 
          {this.state.ok}

          <ModalFormRemboursement show={this.state.show}  onCloseClick={() => this.setState({ show: false })}/>
          
           
          <Container fluid={true}>
          <hr/>
          
          <Row ClassName="mb-4">
            <Col md="6" sm="12" lg="6">
              <label>Terrain</label> 
              <ReactSelect
                closeMenuOnSelect={true}
                components={animatedComponents}
              
                onChange={this.handleChangeTerrain}
                options={this.state.terrain}

                placeholder="Choisir le terrain"
              />
            </Col>
            <Col sm="12" md="6" lg="6">
              <label>Intervalle de date</label>
              <div className="docs-datepicker">
                <InputGroup>
                  <Flatpickr
                    value={pick}
                    className="form-control d-block"
                    placeholder="Slectionnez un intervalle de date"
                    options={{
                      locale:French,
                      mode: "range",
                      altInput: true,
                      // altFormat: "F j, Y",
                      dateFormat: "d-m-y"
                    }}
                     onChange={this.handleDateInterval}
                  />
                  <div className="input-group-append">
                    <button
                      type="button"
                      className="btn btn-outline-secondary docs-datepicker-trigger"
                      disabled
                    >
                      <i
                        className="fa fa-calendar"
                        aria-hidden="true"
                      />
                    </button>
                  </div>
                </InputGroup>
                <div className="docs-datepicker-container" />
              </div>
            </Col>
          </Row>
          <hr/>
          <div >
            <h3 >  <i class='bx bx-notepad bx-sm'></i> Rservatons : <strong>{this.state.solde.nombre_reservation} </strong> </h3>
          </div>
          <hr/>
          <div class="row mt-4" id="divFix">
            <div class="col-sm-4 ">
              <div class="card" id="card5" style={{backgroundColor:"#c7c814"}}>
                <div class="card-body ">
                    <h1 class="card-title" > <strong> {new Intl.NumberFormat().format(this.state.solde_reservation_versement.montantTotalReservations)} FCFA </strong> <i class='bx bx-dollar-circle bx-lg bx-pull-right'></i></h1>
                    <p class="card-text"></p>  
                </div>
                <div class="card-footer">
                  <strong> MONTANT TOTAL (HT) </strong>
                </div>
              </div>
            </div>
            <div class="col-sm-4 ">
              <div class="card " id="card5" style={{backgroundColor:"#14c841"}}>
                <div class="card-body ">
                    <h1 class="card-title"> <strong> {new Intl.NumberFormat().format(this.handleCalculPourcentage(this.state.solde_reservation_versement.montantTotalReservations,this.state.solde_reservation_versement.montantTotalDemandeVersement))} FCFA</strong><i class='bx bx-dollar-circle bx-lg bx-pull-right'></i></h1>
                    <p class="card-text"></p>  
                </div>
                <div class="card-footer">
                  <strong > MON SOLDE (TTC) </strong>
                </div>
              </div>
            </div>
            <div class="col-sm-4 ">
              <div class="card " id="card5" style={{backgroundColor:"#14abc8"}}>
                <div class="card-body ">
                    <h1 class="card-title"> <strong> {new Intl.NumberFormat().format(this.state.solde_reservation_versement.montantTotalDemandeVersement)} FCFA</strong><i class='bx bx-dollar-circle bx-lg bx-pull-right'></i></h1>
                    <p class="card-text"></p>  
                </div>
                <div class="card-footer">
                  <strong> MONTANT TOTAL DES VERSEMENT </strong>
                </div>
              </div>
            </div>
          </div> 
          {/* <div class="row mt-4" id="divFix">
            <div class="col-sm-6 ">
              <div class="card  " id="card5">
                <div class="card-body ">
                    <h1 class="card-title" > <strong> {this.state.solde.nombre_reservation} </strong> <i class='bx bx-notepad bx-lg bx-pull-right'></i></h1>
                    <p class="card-text"></p>  
                </div>
                <div class="card-footer">
                  <strong> NOMBRE DE RESERVATIONS </strong>
                </div>
              </div>
            </div>
          </div> */}

          <Row>
            <Col md="12" className="pull-right">
              <Card>
                <CardBody>
                  <Row ClassName="mb-4">
                    <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                        <h1 className="mb-0 font-size-18">Liste des paiements</h1>
                      </div>
                    </Col>
                    <Col md="4" className="pull-right">
                      <Button
                        color="info"
                        className="font-16 btn btn-info float-right"
                        onClick={this.handleStart}
                      >
                        <i class='bx bx-xs bx-money' > </i>  Demander un virement
                      </Button>
                    </Col>
                  </Row>

                  <hr/>

                  <Row ClassName="mt-5">
                    <Col md="12">
                      <MDBDataTable responsive striped bordered data={data} />
                    </Col>
                  </Row>

                </CardBody>

              </Card>
            </Col>
          </Row>
              
            
            
          </Container>
        </div>
        
      </React.Fragment>
    )
  }
}
export default Finance